<script>
import Card from "@/components/Card.vue";
import { get_user_products } from "@/scripts/apps/product";

export default {
  data() {
    return {
      list: []
    }
  },
  methods: {
    buy() {
      this.$router.push('/apps/cac/buy')
    },
    async refreshList() {
      const loading = this.$vs.loading({
        target: this.$refs.content,
      })
      const resp = await get_user_products(1001);
      if (resp.code !== 1) {
      } else {
        this.list = resp.data;
      }

      loading.close();
    }
  },
  components: {
    Card,
  },
  async mounted() {
    this.refreshList()
  },
};
</script>

